.loader {
	pointer-events: none;
	opacity: 0;
	transition: 0.15s;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}
[data-loading="1"] .loader {
	opacity: 1;
}
.loader svg:first-child {
	-webkit-animation: svg-animation 1s linear infinite;
	animation: svg-animation 1s linear infinite;
	max-width: 64px;
	pointer-events: none;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%,-50%);
	transform: translate(-50%,-50%);
	margin-top: -32px;
	margin-left: -32px;
}
.loader svg:first-child circle {
	display: block;
	fill: transparent;
	stroke: #0F556D;
	stroke-linecap: round;
	stroke-dasharray: 283;
	stroke-dashoffset: 80;
	stroke-width: 6px;
	-webkit-transform-origin: 50% 50%;
	transform-origin: 50% 50%;
}
.loader svg:last-child {
	width: 24px;
}