h1, .h1 {
	font-size: 50px;
	line-height: 60px;
	margin-bottom: 20px;
}
h2, .h2 {
	font-size: 45px;
	line-height: 55px;
	margin-bottom: 20px;
}
h3, .h3 {
	font-size: 26px;
	line-height: 40px;
	margin-bottom: 20px;
}
h4, .h4 {
	font-size: 22px;
	line-height: 26px;
	margin-bottom: 15px;
}
h5 {
	font-size: 18px;
	line-height: 26px;
	margin-bottom: 10px;
}
h6 {
	font-size: 16px;
	line-height: 24px;
	margin-bottom: 10px;
}
p, li, strong {
	font-size: 16px;
	line-height: 24px;
}
small {
	font-size: 12px;
	line-height: 20px;
}
h1, h2, h3, h4, h5, h6 {
	font-weight: bold;
}
h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child {
	margin-bottom: 0;
}
strong {
	font-weight: 500;
}

@media screen and (max-width: 1024px) {
	h1, .h1 {
		font-size: 42px;
		line-height: 53.33px;
	}
	h2, .h2 {
		font-size: 37.33px;
		line-height: 45.33px;
	}
	h3, .h3 {
		font-size: 23.33px;
		line-height: 35.33px;
	}
	h4, .h4 {
		font-size: 20.67px;
		line-height: 24.67px;
	}
	p, li, strong {
		font-size: 15.67px;
		line-height: 23.67px;
	}
}
@media screen and (max-width: 768px) {
	h1, .h1 {
		font-size: 34px;
		line-height: 46.67px;
	}
	h2, .h2 {
		font-size: 29.67px;
		line-height: 35.67px;
	}
	h3, .h3 {
		font-size: 20.67px;
		line-height: 30.67px;
	}
	h4, .h4 {
		font-size: 19.33px;
		line-height: 23.33px;
	}
	p, li, h5, h6, strong {
		font-size: 15.33px;
		line-height: 23.33px;
	}
}
@media screen and (max-width: 480px) {
	h1, .h1 {
		font-size: 26px;
		line-height: 40px;
	}
	h2, .h2 {
		font-size: 22px;
		line-height: 26px;
	}
	h3, .h3 {
		font-size: 18px;
		line-height: 26px;
	}
	h4, .h4 {
		font-size: 18px;
		line-height: 22px;
	}
	p, li, h5, h6, strong {
		font-size: 15px;
		line-height: 23px;
	}
}
