a {
	text-decoration: none;
	position: relative;
	font-size: inherit;
	font-weight: inherit;
	line-height: inherit;
	color: inherit;
}
a:not(.btn):not(.arev):not(.nohov):before {
	content: '';
	position: absolute;
	bottom: 1px;
	left: 0;
	right: 100%;
	height: 1px;
	transition: 0s;
	-moz-transition: 0s;
	-webkit-transition: 0s;
	background-color: #000;
	pointer-events: none;
}
a:not(.btn):not(.arev):not(.nohov):hover:before {
	right: 0;
	transition: 0.3s;
	-moz-transition: 0.3s;
	-webkit-transition: 0.3s;
}
a:not(.btn):not(.arev):not(.nohov):after {
	content: '';
	position: absolute;
	bottom: 1px; /*-4 2*/
	left: 100%;
	right: 0;
	height: 1px;
	transition: 0.3s;
	-moz-transition: 0.3s;
	-webkit-transition: 0.3s;
	background-color: #000;
	pointer-events: none;
}
a:not(.btn):not(.arev):not(.nohov):hover:after {
	left: 0;
	transition: 0s;
	-moz-transition: 0s;
	-webkit-transition: 0s;
	transition-delay: 0.3s;
	-moz-transition-delay: 0.3s;
	-webkit-transition-delay: 0.3s;
}
a:not(.btn):not(.arev):not(.nohov).fwb_:before,
a:not(.btn):not(.arev):not(.nohov).fwb_:after,
a:not(.btn).fwb_:hover:before,
a:not(.btn).fwb_:hover:after {
	bottom: 0;
	height: 2px;
}
small a:not(.btn):not(.arev):not(.nohov):before,
small a:not(.btn):not(.arev):not(.nohov):after {
	bottom: -1px;
	height: 1px;
}
a:not(.btn):not(.arev):not(.nohov).white:before,
a:not(.btn):not(.arev):not(.nohov).white:after {
	background-color: white;
}
a:not(.btn).arev:before {
	content: '';
	position: absolute;
	bottom: 1px;
	left: 0;
	right: 0;
	height: 1px;
	transition: 0.3s;
	-moz-transition: 0.3s;
	-webkit-transition: 0.3s;
	background-color: #000;
	pointer-events: none;
}
a:not(.btn).arev:after {
	content: '';
	position: absolute;
	bottom: 1px;
	left: 0;
	right: 0;
	height: 1px;
	transition: 0s;
	-moz-transition: 0s;
	-webkit-transition: 0s;
	transition-delay: 0.3s;
	-moz-transition-delay: 0.3s;
	-webkit-transition-delay: 0.3s;
	background-color: #000;
	pointer-events: none;
}
a:not(.btn).arev:hover:before {
	right: 100%;
	transition: 0s;
	-moz-transition: 0s;
	-webkit-transition: 0s;
}
a:not(.btn).arev:hover:after {
	left: 100%;
	transition: 0.3s;
	-moz-transition: 0.3s;
	-webkit-transition: 0.3s;
}
a:not(.btn).blue_1:before,
a:not(.btn).blue_1:after {
	background-color: #0F556D !important;
}
a:not(.btn).gray_1:before,
a:not(.btn).gray_1:after {
	background-color: #B1B1B1 !important;
}
a:not(.btn).gray_2:before,
a:not(.btn).gray_2:after {
	background-color: #4A4A4A !important;
}

a.btn {
	padding: 14px 30px;
	border-radius: 22px;
	transition: 0.25s;
	position: relative;
	display: inline-block;
}
a.btn.btn_sm {
	padding: 6px 18px;
}
a.btn.btn_sm span {
	font-size: 12px;
	line-height: 20px;
}
a.btn.btn_md {
	padding: 7px 20px;
}
a.btn.btn_md span {
	text-transform: none;
	font-size: 14px;
	line-height: 26px;
}
a.btn span {
	color: #0F556D;
	font-weight: 500;
	text-transform: uppercase;
	display: inline-block;
	transform: translate(0, 2px);
	transition: 0.25s;
}
a.btn.fwb_ span {
	font-weight: bold;
}
a.btn:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	pointer-events: none;
	border-radius: 22px;
	transition: 0.25s;
}
a.btn:before {
	border: 2px solid #0F556D;
}
a.btn:hover:before,
a.btn[data-active]:before {
	background: #0F556D;
}
a.btn:hover span,
a.btn[data-active] span {
	color: #fff;
}
a.btn.btn_blue span {
	color: #fff;
}
a.btn.btn_blue:not([data-inactive="1"]):hover span {
	color: #9FBBC4;
}
a.btn.btn_blue:before {
	background: #0F556D;
}
/*a.btn.btn_blue:not(.inactive):hover:before {
	background: #fff;
}*/
a.btn.btn_blue[data-inactive="1"] {
	opacity: 0.5;
	cursor: not-allowed;
}
a.btn[data-inactive="1"] span {
	opacity: 0.5;
}
a.btn.shadow {
	-webkit-box-shadow: 2px 10px 30px 0px rgba(15,85,109,0.5);
	-moz-box-shadow: 2px 10px 30px 0px rgba(15,85,109,0.5);
	box-shadow: 2px 10px 30px 0px rgba(15,85,109,0.5);
}
a.btn.shadow:hover {
	-webkit-box-shadow: 2px 10px 30px -10px rgba(15,85,109,0.5);
	-moz-box-shadow: 2px 10px 30px -10px rgba(15,85,109,0.5);
	box-shadow: 2px 10px 30px -10px rgba(15,85,109,0.5);
}
a.btn.btn_rect,
a.btn.btn_rect:before {
	border-radius: 7px;
}

.a_svg {
	display: inline-block;
}
.a_svg svg {
	position: absolute;
}
.a_svg svg [data-fillchange] {
	transition: 0.25s;
}
.a_svg:hover svg [data-fillchange] {
	fill: #fff;
}

.a_svg_qr svg {
	transform: translate(0, 3px);
}
.a_svg_qr span {
	padding-left: 28px;
}
.a_svg.a_svg_qr:hover svg [data-fillchange] {
	fill: #9FBBC4;
}
.a_svg_location svg {
	width: 12px;
	height: 12px;
	transform: translate(-4px, 4px);
}
.a_svg_location span {
	padding-left: 12px;
}

