textarea,
input {
	outline: none;
	font-family: Avenir, Arial;
	overflow-x: hidden;
}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
	color: #A8A8A8;
	font-size: 16px;
	line-height: 24px;
	font-family: Avenir, Arial;
	font-weight: 300;
}
::-moz-placeholder { /* Firefox 19+ */
	color: #A8A8A8;
	font-size: 16px;
	font-family: Avenir, Arial;
	opacity: 1 !important;
	font-weight: 300;
}
:-ms-input-placeholder { /* IE 10+ */
	color: #A8A8A8;
	font-size: 16px;
	line-height: 24px;
	font-family: Avenir, Arial;
	font-weight: 300;
}
:-moz-placeholder { /* Firefox 18- */
	color: #A8A8A8;
	font-size: 16px;
	line-height: 24px;
	font-family: Avenir, Arial;
	font-weight: 300;
}

.input_cont,
.input_dropdown {
	position: relative;
	margin-bottom: 10px;
}
.input_cont:last-child,
.input_dropdown:last-child {
	margin-bottom: 0;
}
.input_cont .error_cont {
	color: #FF8780;
	font-weight: 500;
	padding: 7px 0 0 0;
	transition: 0.5s;
	transition-delay: 0.5s;
	font-size: 16px;
	line-height: 24px;
}
input[type="email"],
input[type="number"],
input[type="password"],
input[type="text"],
textarea {
	height: 50px;
	background: #fafbff;
	font-size: 16px;
	line-height: 24px;
	color: #4A4A4A;
	font-weight: 500;
	border: 1px solid #eee;
	border-radius: 5px;
	width: 100%;
	padding: 8px 25px 0 25px;
}
/*.textinput .secret_eye {
	display: none;
	pointer-events: none;
}*/
textarea {
	height: auto;
	min-height: 50px;
	resize: vertical;
	padding-top: 14px;
	padding-bottom: 12px;
}
input[type="text"] + label,
textarea + label {
	position: absolute;
	font-size: 16px;
	line-height: 24px;
	left: 25px;
	transition: 0.25s;
	font-weight: 300;
	color: #A8A8A8;
	pointer-events: none;
	width: calc(100% - 60px);
	transform: translateY(15px);
}
input[data-focused="1"] + label,
textarea[data-focused="1"] + label {
	transform: translateY(-2px);
	font-size: 10px;
}
input[type="text"] + label,
textarea[data-focused="1"] + label {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}


/* search fields */
.input_cont.search input {
	background: #f4f5f7;
	font-weight: bold;
	border-radius: 25px;
	padding-right: 50px;
}
.input_cont.search > div:first-child:after {
	content: '';
	background: url('../../svg/search.svg');
	background-repeat: no-repeat;
	background-size: 20px;
	position: absolute;
	width: 20px;
	height: 20px;
	top: 50%;
	transform: translateY(-50%);
	right: 25px;
	pointer-events: none;
}

/* checkboxes */
input[type="checkbox"] {
	position: absolute;
	width: 0;
	height: 0;
	visibility: hidden;
}
input[type="checkbox"] + label:before {
	content: '';
	position: absolute;
	display: block;
	width: 16px;
	height: 16px;
	border: 2.5px solid #fff;
	border-radius: 5px;
	cursor: pointer;
}
input[type="checkbox"] + label:after {
	content: '';
	position: absolute;
	display: block;
	width: 0;
	height: 18.5px;
	margin-top: -2px;
	margin-left: 1px;
	transition: 0.15s;
	background-repeat: no-repeat;
	background-image: url('../../svg/ok_b.svg');
	background-size: 21px 18.5px;
	background-position: 0 0;
	z-index: 2;
	cursor: pointer;
}
.checkbox + strong {
	padding-left: 28px;
}
input[type="checkbox"]:checked + label:after {
	width: 21px;
	margin-left: 3px;
}