.enter_menu {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-content: stretch;
	min-height: 100%;
}
.enter_menu > div {
	display: flex;
	justify-content: center;
	flex-direction: column;
	padding-left: 30px;
	padding-right: 30px;
	align-items: center;
	width: 100%;
	max-width: 900px;
}
.enter_menu > div > div:first-child {
	width: 100%;
	background: white;
	border-radius: 10px;
	display: flex;
	justify-content: center;
	padding: 45px 0;
	margin-top: 30px;
	margin-bottom: 30px;
	min-height: 487px;
}
.enter_menu > div > div:first-child > div {
	display: flex;
	flex-direction: column;
	width: 360px;
	padding: 0 30px;
}
.enter_menu > div > div:first-child > div .upper {
	display: flex;
	text-align: center;
	flex-direction: column;
	margin-bottom: 5px;
}
.enter_menu > div > div:first-child > div .upper .logo {
	margin-bottom: 15px;
}
.enter_menu > div > div:first-child > div .upper strong:nth-child(2) {
	margin-bottom: 5px;
}
.enter_menu > div > div:first-child > div .title_cont {
	margin: 15px 0 5px 0;
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
}
.enter_menu > div > div:first-child > div .title_cont h6 {
	margin-bottom: 0;
	padding-right: 10px;
}
.enter_menu > div > div:first-child > div .button_cont {
	margin-top: 20px;
}
.enter_menu .login_menu > div:first-child {
	margin-bottom: 0;
}
.enter_menu .login_menu > div:last-child {
	display: flex;
	text-align: center;
	flex-direction: column;
	margin-top: 30px;
}
.enter_menu .password_forgot_menu > div:first-child {
	margin-bottom: 77px;
}

.enter_menu .register_menu > div:first-child {
	min-height: 682px;
}
.enter_menu .register_confirmed > div:first-child {
	min-height: auto;
}

.enter_menu .logo > svg {
	width: 74px;
}

.enter_menu form {
	opacity: 1;
	transition: 0.25s;
}
.enter_menu[data-loading="1"] form {
	opacity: 0;
	pointer-events: none;
}