.input_dropdown .select {
	cursor: pointer;
	height: 50px;
	background: #fff;
	font-size: 16px;
	line-height: 24px;
	border: 0;
	border-radius: 5px;
	width: 100%;
	padding: 0 40px 0 25px;
	display: flex;
	align-items: center;
}
.input_dropdown .select > span {
	color: #A8A8A8;
	font-weight: 300;
	pointer-events: none;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}
.input_dropdown .dropdown {
	background: white;
	position: absolute;
	width: 100%;
	z-index: 1;
	transition: 0.25s;
	top: 53px;
	border-radius: 5px;
	opacity: 0;
	pointer-events: none;
}
.input_dropdown[data-open="1"] .dropdown {
	opacity: 1;
	pointer-events: initial;
}
.input_dropdown .dropdown > div {
	height: 50px;
	padding: 0 20px;
	display: flex;
	align-items: center;
	position: relative;
	cursor: pointer;
	transition: 0.25s;
	color: #0F556D;
	font-weight: bold;
}
.input_dropdown .dropdown > div:hover {
	color: #9FBBC4;
}
.input_dropdown .dropdown > div:after {
	content: '';
	background: #B1B1B1;
	height: 1px;
	width: 100%;
	position: absolute;
	bottom: 0;
	left: 0;
	opacity: 0.3;
}
.input_dropdown .dropdown > div:last-child:after {
	content: none;
}
.input_dropdown > div:after {
	content: '';
	width: 18px;
	height: 11px;
	position: absolute;
	right: 17px;
	top: 20px;
	background: url('../../svg/arr_d_g.svg');
	background-repeat: no-repeat;
	transition: 0.25s;
	pointer-events: none;
}
.input_dropdown[data-open="1"] > div:after {
	transform: rotate(180deg);
}
.input_dropdown[data-selected="1"] .select > span {
	color: #0F556D;
	font-weight: bold;
}